.card {
  /* position: relative; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 548px;
  width: 331px;
  border: 1px solid red;
  padding: 24px;
  background-color: #fff;
  border-radius: 12px;
}

.imgBlock {
  position: relative;
}

.hitNew {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  gap: 8px;
  padding: 0;
  margin: 0;
}

.hit {
  /* position: absolute; */
  /* top: 0; */
  color: #F63;
  /* font-feature-settings: 'clig'off, 'liga'off; */
  /* font-family: Inter; */
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  background-color: rgba(251, 220, 213, 1);
  border-radius: 8px;
  padding: 6px 10px;
}

.new {
  /* position: absolute; */
